<template>
    <div id="classify">
        <div id="find" >
            <div class="find-cent">
                <div class="find-logo"></div>
                <input type="text" placeholder="" required="required" @click="showsear()">
            </div>
        </div>
        <div id="seek">
            <div class="seek-top">
                <div class="seek-l"  @click="hidesear()"><i>&#xe630;</i></div>
            <div class="seek-c">
                <div class="find-logo"></div>
                <input type="text" placeholder="" required="required">
            </div>    
            <div class="seek-r"></div>
            </div>
            
            <div class="search-hot-wrap">
                <div class="sear-hot"></div>
                <div class="sear-cent">
                    <a href=""></a>
                    <a href=""></a>
                    <a href=""></a>
                    <a href=""></a>
                    <a href=""></a>
                    <a href=""></a>
                    <a href=""></a>
                    <a href=""></a>
                    <a href=""></a>
                    <a href=""></a>
                    <a href=""></a>
                    <a href=""></a>
                </div>
                <div class="sear-history"></div>
                <div class="sear-foo"></div>
            </div> 
        </div>

        <div id="cla-content" >
            <ul class="cla-con-l">
                <li @click.prevent="showGoods()"></li>
                <li @click.prevent="showGoods()"></li>
                <li @click.prevent="showGoods()"></li>
                <li @click.prevent="showGoods()"></li>
                <li @click.prevent="showGoods()"></li>
                <li @click.prevent="showGoods()"></li>
                <li @click.prevent="showGoods()"></li>
                <li @click.prevent="showGoods()"></li>
                <li @click.prevent="showGoods()"></li>
                <li @click.prevent="showGoods()"></li>
            </ul>
            <div class="cla-con-r">
                <dl class="ccr" v-for="sgs in classify" :key="sgs.guid">
                    <dt>{{sgs.sgsctit}}</dt>
                    <dd>
                        <div v-for="aaa in sgs.sgimg" :key="aaa.id">
                            <img :src="aaa.img" alt="">
                            <span>{{aaa.sgtit}}</span>
                        </div>
                    </dd>
                </dl>
                
            </div>
        </div>
</div>
</template>

<script>
import "../assets/css/classify.css";
import "../assets/iconfont/iconfont.css";
export default {
  data() {
    return {
      
      classify: [],
    };
  },
  methods: {
    showGoods() {
      this.jQuery.get(this.apiURL + "/classify", result => {
      console.log(result.sgsc)
      this.classify = result.sgsc;
      });
    },
    //
    showsear() {
      var $ = this.jQuery;
      $("#find").css("display", "none");
      $("#seek").css("display", "block");
    },
    //
    hidesear() {
      var $ = this.jQuery;
      $("#find").css("display", "block");
      $("#seek").css("display", "none");
    }
  },
  created() {
    this.jQuery.get(this.apiURL + "/classify", result => {
      console.log(result.sgsc)
      this.classify = result.sgsc;
      var $ = this.jQuery;
      //
      lh(0); 
      $(".cla-con-l li").click(function() {
        var index = $(this).index();
        lh(index);
      });
      function lh(index) {
        $(".cla-con-l li").eq(index).addClass("lihover").siblings().removeClass("lihover");
      }
    });
  }
};
</script>





