import Vue from 'vue'
import Router from 'vue-router'
import shop from './components/shop.vue'
import home from './components/home.vue'
import footer from './components/footer.vue'
import classify from './components/classify.vue'
import shopcar from './components/shopcar.vue'
import indent from './components/indent.vue'
import mine from './components/mine.vue'
import login from './components/login.vue'
import jdregist from './components/jdregist.vue'
import list from './components/list.vue'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      components: {
        default: home,
        footer: footer
      }
    },
    {
      path: '/shop/:guid/:title',
      component: shop,
      props: true
    },
    {
      path: '/classify',
      components: {
        default: classify,
        footer: footer
      }
    },
    {
      path: '/shopcar',
      components: {
        default: shopcar,
        footer: footer
      }
    },
    {
      path: '/indent',
      components: {
        default: indent,
        footer: footer
      }
    },
    {
      path: '/mine',
      components: {
        default: mine,
        footer: footer
      }
    },
    {
      path: '/login',
      components: {
        default: login,
      }
    },
    {
      path: '/jdregist',
      components: {
        default: jdregist,

      }
    },
    {
      path: '/list/:guid',
      components: {
        default: list
      }, 
      props: true
    }
  ]
})



