<script>
import infiniteScroll from "vue-infinite-scroll";
import "swiper/dist/css/swiper.min.css";
import Swiper from "Swiper";
import "../assets/iconfont/iconfont.css";
import "../assets/css/home.css";
export default {
  directives: { infiniteScroll: infiniteScroll },
  data() {
    return {
      busy: false,
      shops: []
    };
  },
  methods: {
    loadMore() {
      this.busy = true;
      this.jQuery.get(this.apiURL + "/shops", result => {
        this.shops = this.shops.concat(result.data);
        this.busy = false;
      });
    }
  },
  created() {
    this.jQuery.get(this.apiURL + "/shops", result => {
      this.shops = result.data;
    });
    var $ = this.jQuery;
    $(window).scroll(function(event) {
      //
      var cpnum = 1;
      $(".cpa").click(function() {
        if (cpnum) {
          $(this).css("height", "auto");
          cpnum = 0;
        } else {
          cpnum = 1;
          $(this).css("height", "55px");
        }
        console.log(cpnum)
      });
      //
      var top = $(this).scrollTop();
      var al = top / 106;
      if (al > 1) {
        return;
      }
      $(".search").css("background", "rgba(75, 201, 85, " + (1 - al) + ")");
      $(".topnav").css("background", "rgba(75, 201, 85, " + al + ")");
      if (top <= 10) {
        $(".topnav .tn-r").css({
          marginRight: "-25px",
          transition: "1s"
        });
      } else {
        $(".topnav .tn-r").css({
          marginRight: "5px",
          transition: "1s"
        });
      }
    });
  },

  mounted() {
    //
    var mySwiper = new Swiper(".swiper-container", {
      loop: false,
      autoplay: {
        delay: 2000,
        stopOnLastSlide: false,
        disableOnInteraction: false
      },
      pagination: {
        el: ".swiper-pagination"
      }
    });
  }
};
</script>




