<template>
    <div id="footer">
      <a  class="index" href="#/home"></a>
      <a  class="hcs" href="#/classify"></a>
      <a  class="cartlist" href="#/shopcar"></a>
      <a  class="orderlist" href="#/indent"></a>
      <a  class="mine" href="#/mine"></a>
    </div>
</template>
<script>
import infiniteScroll from "vue-infinite-scroll";
export default {
  directives: { infiniteScroll: infiniteScroll },
  data() {
    return {
      
    };
  },
  methods: {
   
  },
  created() {
      var $ = this.jQuery;
      window.onload = function() { 
        change(0); 
          $("#footer a").click(function() {
            var index=$(this).index();
              change(index);
          });
          function change(index){
            $("#footer a").eq(index).addClass('active').siblings().removeClass('active')
          }
        };
  }
};
</script>
<style>
#footer {
  width: 100%;
  height: 50px;
  border-top: 1px solid #d9d9d9;
  background: white;
  position: fixed;
  bottom: 0;
  z-index: 11;
}
#footer a {
  float: left;
  width: 20%;
  height: 50px;
  font-size: 12px;
  line-height: 17px;
  text-align: center;
  padding-top: 30px;
  vertical-align: top;
  color: #555;
  background-image: url(/static/img/nav4.png);
  background-repeat: no-repeat;
  background-size: 24px 600px;
  text-decoration: none;
}
#footer a:nth-child(1){background-position: center -250px;}
#footer a:nth-child(2){background-position: center -300px;}
#footer a:nth-child(3){background-position: center -350px;}
#footer a:nth-child(4){background-position: center -400px;}
#footer a:nth-child(5){background-position: center -450px;}
#footer a:nth-child(1).active{background-position: center 0px;color: #39ac69;}
#footer a:nth-child(2).active{background-position: center -50px;color: #39ac69;}
#footer a:nth-child(3).active{background-position: center -100px;color: #39ac69;}
#footer a:nth-child(4).active{background-position: center -150px;color: #39ac69;}
#footer a:nth-child(5).active{background-position: center -200px;color: #39ac69;}
</style>



