<script>
import "../assets/css/shop.css";
import "../assets/iconfont/iconfont.css";
import car from "./car";
export default {
  props: ["guid", "title"],
  data() {
    return {
      ctgs: [],
      goods: [],
      ctgText: "",
      count: "",
      alinum: 1,
      bmnum: 1
    };
  },
  components: {
    car
  },
  created() {
    //
    if (!this.bus.cars[this.guid]) {
      this.$set(this.bus.cars, this.guid, []);
    }
    this.jQuery.get(this.apiURL + "/ctgs", { guid: this.guid }, result => {
      if (result.errcode == 0) {
        this.ctgs = result.data;
      }
    });
    this.jQuery.get(this.apiURL + "/goods", result => {
      if (result.errcode == 0) {
        this.goods = result.data;
        this.ctgText = result.ctgText;
        this.count = result.count;
      }
    });
  },
  methods: {
    showGoods(text) {
      this.jQuery.get(this.apiURL + "/goods", { ctgtext: text }, result => {
        if (result.errcode == 0) {
          this.goods = result.data;
          this.ctgText = result.ctgText;
          this.count = result.count;
        }
      });
    },

    addCar(gd) {
      //
      var car = this.bus.cars[this.guid];
      for (var i = 0; i < car.length; i++) {
        if (car[i].guid == gd.guid) {
          if (this.bus.cars[this.guid][i].num <= gd.store) {
            this.bus.cars[this.guid][i].num++;
            //
            this.bus.cars[this.guid].splice(i, 1, this.bus.cars[this.guid][i]);

            this.$set(
              this.bus.carmuns,
              gd.guid,
              this.bus.cars[this.guid][i].num
            );
          }
          return;
        }
      }
      gd.num = 1; //
      if (gd.num >= gd.store) {
        gd.num = gd.store;
      }
      this.bus.cars[this.guid].push(gd);
      this.$set(this.bus.carmuns, gd.guid, gd.num);
    },
    subCar(gd) {
      var car = this.bus.cars[this.guid];
      for (var i = 0; i < car.length; i++) {
        if (car[i].guid == gd.guid) {
          var num = --this.bus.cars[this.guid][i].num;
          this.bus.cars[this.guid].splice(i, 1, this.bus.cars[this.guid][i]);

          if (num <= 0) {
            delete this.bus.carmuns[gd.guid];
            this.bus.cars[this.guid].splice(i, 1);
          }
          this.$set(this.bus.carmuns, gd.guid, num);
          return;
        }
      }
    },
    act() {
      var $ = this.jQuery;
      $("#shop").css("height", "auto");
      $("#car").css("display", "none");
      $(".st-dd").css("display", "block");
      $(".st-cc").css("display", "none");
    },
    std() {
      var $ = this.jQuery;
      $("#shop").css("height", "105px");
      $("#car").css("display", "block");
      $(".st-cc").css("display", "block");
      $(".st-dd").css("display", "none");
    },
    ste() {
      var $ = this.jQuery;
      $(".stbu-most").css("height", "470px");
      $(".st-ff").css("display", "block");
      $(".st-ee").css("display", "none");
    },
    stf() {
      var $ = this.jQuery;
      $(".stbu-most").css("height", "0px");
      $(".st-ee").css("display", "block");
      $(this).css("display", "none");
    },
    //
    showsear() {
      var $ = this.jQuery;
      $(".shop-find")
        .css("display", "none")
        .addClass("sfpt");
      $(".shop-seek")
        .css("display", "block")
        .addClass("sfpt");
      $("#shop").css("display", "none");
    },
    //
    hidesear() {
      var $ = this.jQuery;
      $(".shop-find")
        .css("display", "block")
        .removeClass("sfpt");
      $(".shop-seek")
        .css("display", "none")
        .removeClass("sfpt");
      $("#shop").css("display", "block");
    },
    //
    ali() {
      var $ = this.jQuery;
      if (this.alinum) {
        $(".alia")
          .parent()
          .css("height", "auto");
        this.alinum = 0;
      } else {
        $(".alia")
          .parent()
          .css("height", "46px");
        this.alinum = 1;
      }
    },
    bli() {
      var $ = this.jQuery;
      $(".slbul li").click(function() {
        $(".bli a")
          .css("color", "#47b34f")
          .siblings()
          .css("color", "#666");
      });
    },
    // 
    ahbm() {
      var $ = this.jQuery;
      if (this.bmnum) {
        $(".bmenu").css("display", "block");
        this.bmnum = 0;
      } else {
        $(".bmenu").css("display", "none");
        this.bmnum = 1;
      }
    }
  }
};
</script>



