<template>
<div id="logincontainer">
    <div id="login">
        <div class="lg-head">
            <a  href="#/mine" class="lgh-l"></a>
            <span class="lgh-r"></span>
        </div>
        <div class="page">
            <div class="pg-l">
                <span class="pgls" @click="pgls()"></span>
            </div>
            <div class="pg-r">
                <span class="pgrs" @click="pgrs()"></span>
            </div>
        </div>
        <div class="lg-wrap">
            <div class="unlogin">
                <div class="ulun">
                    <div class="un-aa"></div>
                   
                    <input type="text" class="lgname" placeholder="//" v-model="name" @focus="lgfocus()" @blur="lgblur()">
                    <i class="un-cc"></i>
                </div>
                <div class="ulpw">
                    <div class="uw-aa"></div>
                    <input :type="type" class="lgpwd" placeholder="" v-model="password" @focus="lgpwdfo()" @blur="lgpwdbl()">
                    <i class="un-dd"></i>
                    <label class="un-ee">
                        <div class="un-ff" @click="typeqh()"></div>
                    </label>
                </div>
            </div>
        </div>
            <div class="phlogin">
                <div class="pgcon">
                    <div class="pgph">
                    <label class="pg-aa">
                        <span class="pgas">+86</span>
                        <i class="pgai"></i>
                    </label>
                    <input type="text" class="pginp" placeholder="" v-model="pgho">
                    <i class="pgi"></i>  
                    </div>
                    <span class="pgbtn" @click="pgbtn()"></span>
                </div>
                <div class="pgyzm">
                    <span class="pgy-cc"></span>
                    <input type="text" class="pgy-aa" placeholder="" v-model="pgyzm">

                    <i class="pgy-bb"></i>
                </div>
            </div>
            <div class="loginfoo">
                <p class="lgtis"></p>
                 <a class="loginbtn" @click="lbtn()"> </a>
             <a href="" class="loginonebtn"></a>
            <div class="meqic">
                <a href="" class="mq-aa">
                    <i class="mqai"></i>
                    <span class="mqab"></span>
                </a>
                 <a href="" class="mq-bb">
                    <i class="mqbi"></i>
                    <a class="mqbb" href="#/jdregist"></a>
                </a>
            </div>
            <div class="quick-login">
                <h4 class="qlh4"></h4>
                <div class="qlca">
                    <i class="qlai"></i>
                    <i class="qlbi"></i>
                </div>                
            </div>    
            <div class="agree">
                <p class="agrp">
                    
                    <a href=""></a>
                </p>
            </div>
            </div>
            

    </div>
</div>

</template>

<script>
import "../assets/css/login.css";
import "../assets/iconfont/iconfont.css";
export default {
  data() {
    return {
      name: "",
      password: "",
      type: "password",
      tynum: 1,
      parun: "/^[a-zA-Z]w{2,8}$/",
      countdown: 5,
      pgho: "",
      pgyzm: ""
    };
  },
  methods: {
    pgls() {
      var $ = this.jQuery;
      $(".pgls").css("border-bottom", "2px solid #f23030");
      $(".pgrs").css("border-bottom", "none");
      $(".lg-wrap").css("display", "block");
      $(".phlogin").css("display", "none");
      $(".lgtis").html("");
    },
    pgrs() {
      var $ = this.jQuery;
      $(".pgrs").css("border-bottom", "2px solid #f23030");
      $(".pgls").css("border-bottom", "none");
      $(".lg-wrap").css("display", "none");
      $(".phlogin").css("display", "block");
      $(".lgtis").html("");
    },
    typeqh() {
      var $ = this.jQuery;
      var _this = this;
      if (this.tynum) {
        _this.type = "text";
        _this.tynum = 0;
        $(".un-ff").css(
          "background-image",
          "url(/static/img/show_password.png)"
        );
      } else {
        _this.type = "password";
        _this.tynum = 1;
        $(".un-ff").css(
          "background-image",
          "url(/static/img/hide_password.png)"
        );
      }
    },
    //
    lgfocus() {
      var $ = this.jQuery;
      $(".un-cc").css("display", "block");
      $(".lgtis").html("");
    },
    ///
    lgblur() {
      var $ = this.jQuery;
      if (this.name != "18895395906") {
        $(".lgtis").html("");
      }
      $(".un-cc").css("display", "none");
    },
    //
    lgpwdfo() {
      var $ = this.jQuery;
      $(".un-dd").css("display", "block");
      $(".lgtis").html("");
    },
    lgpwdbl() {
      var $ = this.jQuery;
      if (this.password != "123123") {
        $(".lgtis").html("");
      }
      $(".un-dd").css("display", "none");
    },
    pgbtn() {
      var $ = this.jQuery;
      //   $(".pgbtn").html("").css("font-size", "13px");
      $(".pgy-cc").css("display", "block");
      var arr = [];
      var str = "";
      for (var i = 97; i <= 122; i++) {
        arr.push(String.fromCharCode(i));
      }
      for (var j = 65; j <= 90; j++) {
        arr.push(String.fromCharCode(j));
      }
      for (var k = 48; k <= 57; k++) {
        arr.push(String.fromCharCode(k));
      }
      for (var a = 0; a < 4; a++) {
        var index = parseInt(Math.random() * arr.length);
        str += arr[index];
        $(".pgy-cc").html(str);
      }
      //
      var countdown = 5;
      var ctime = setInterval(function() {
        var _this = this;
        if (countdown == 0) {
          $(".pgbtn")
            .html("")
            .css("color", "#d7d7d7");
          countdown = 5;
          clearInterval(ctime);
        } else {
          $(".pgbtn")
            .html("(" + countdown + ")")
            .css({ "font-size": "13px", color: "#f23030" });
          countdown--;
        }
      }, 1000);
    },
    lbtn() {
      var $ = this.jQuery;
      var pgyc = $(".pgy-cc");
      console.log(pgyc.html());
      var pgya = $(".pgy-aa");
      console.log(pgya.val());
      if (pgya.val() != pgyc.html()) {
        $(".pgy-cc").html("");
      } else {
        window.open("#/mine", "_self");
      }
    }
  },

  created() {},
  updated() {
    //
    var $ = this.jQuery;
    if (this.name != "" && this.password != "") {
      $(".loginbtn").css({ background: "#f23030", color: "white" });
    } else {
      $(".loginbtn").css("background", "#eee");
    }

    if (this.pgho != "" && this.pgyzm != "") {
      $(".loginbtn").css({ background: "#f23030", color: "white" });
    } else {
      $(".loginbtn").css("background", "#eee");
    }
  }
};
</script>



