<template>
  <div id="car" >
    <div class="empcar" v-if="totalnum<1">
      <a class="ecl"></a>
      <div class="ecc"></div>
      <a  class="ecr"></a>
    </div>
    <div class="fulcar" v-if="totalnum>=1">
      <a class="fullogo" @click=" fgcli()"><i>{{totalnum}}</i></a>
      <i class="fullogo-icon"></i>
        <div class="fullgd">
         
          <div class="fgqx">
            <span class="fx-aa" @click="qx()"></span>
            <a class="fx-bb" @click="emptyCar"></a>
          </div>
          <div class="flgds">
            <div class="flgd-aa">
              <ul class="flgd-bb">
                <li class="flgd-cc" v-for="car in bus.cars[shopid]" :key="car.guid">
                  <span class="flgd-dd"></span>
                  <a class="flgd-ff">
                    <table>
                      <tbody>
                        <tr>
                          <td class="flgd-img"><img :src="car.img" alt=""></td>  
                          <td class="flgd-con">
                              <div class="flgd-tit">{{car.srtit}}</div>
                              <div class="flgd-pri">
                                <span></span>
                                <div class="fp-bb">
                                  &yen;{{car.price}}
                                  <del>&yen;{{car.delpri}}</del>
                                </div>
                              </div>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </a>
                  <a class="flgd-cel" @click="subCar(car)"></a>
                  <em class="flgd-num"> {{car.num}}</em>
                  <a class="flgd-add" @click="addCar(car)"></a>
                </li>
              </ul>
            </div>
          </div>
        </div>
      <div class="fullpc">
        <div class="fullpri">
        <div>&yen;{{total|currency}}</div>
      </div>
      <a class="fullclear"></a>
      </div>     
    </div>
     <div class="fulcover" @click="covcli()"></div>
  </div>
</template>
<script>
import "../assets/css/car.css";
import "../assets/iconfont/iconfont.css";
export default {
  props: ["shopid"],
  data() {
    return {
      checked: [],
      qxsum: 1,
      fgsum: 1
    };
  },
  filters: {
    currency(value) {
      value = Number(value);
      return value.toFixed(2);
    }
  },
  computed: {
    //
    total() {
      //
      var sum = 0;
      var $=this.jQuery
      for (var i = 0; i < this.bus.cars[this.shopid].length; i++) {
        var gid = this.bus.cars[this.shopid][i].guid;
        // if(this.checked.indexOf(gid)==-1){
        //   continue
        // }
          sum +=
          this.bus.cars[this.shopid][i].price *
          this.bus.cars[this.shopid][i].num;
       
        
      }
      return sum;
    },
    totalnum() {
      //
      var sum = 0;
      for (let i = 0; i < this.bus.cars[this.shopid].length; i++) {
        sum += this.bus.cars[this.shopid][i].num;
      }
      return sum;
    }
  },
  created: {},
  methods: {
    emptyCar() {
      //
      this.$set(this.bus.cars, this.shopid, []);
      this.$set(this.bus, "carmuns", {});
      this.jQuery(".fulcover").css("display", "none");
      this.jQuery(".fulcar").css("height", "50px");
      this.jQuery(".fullogo").css("top", "-13px");
    },

    addCar(gd) {
      //
      var car = this.bus.cars[this.shopid];
      for (var i = 0; i < car.length; i++) {
        if (car[i].guid == gd.guid) {
          if (this.bus.cars[this.shopid][i].num <= gd.store) {
            this.bus.cars[this.shopid][i].num++;
            //
            this.bus.cars[this.shopid].splice(
              i,
              1,
              this.bus.cars[this.shopid][i]
            );

            this.$set(
              this.bus.carmuns,
              gd.guid,
              this.bus.cars[this.shopid][i].num
            );
          }
          return;
        }
      }
      gd.num = 1; //
      if (gd.num >= gd.store) {
        gd.num = gd.store;
      }
      this.bus.cars[this.shopid].push(gd);
      this.$set(this.bus.carmuns, gd.guid, gd.num);
    },
    subCar(gd) {
      var car = this.bus.cars[this.shopid];
      for (var i = 0; i < car.length; i++) {
        if (car[i].guid == gd.guid) {
          var num = --this.bus.cars[this.shopid][i].num;
          this.bus.cars[this.shopid].splice(
            i,
            1,
            this.bus.cars[this.shopid][i]
          );

          if (num <= 0) {
            delete this.bus.carmuns[gd.guid];
            this.bus.cars[this.shopid].splice(i, 1);
          }
          this.$set(this.bus.carmuns, gd.guid, num);
          return;
        }
      }
    },
    qx() {
      var $ = this.jQuery;
        if (this.qxsum) {
          $(".fx-aa").css("backgroundPosition", "10px 10px");
          $(".flgd-dd").css("backgroundPosition", "10px 10px");
          this.qxsum = 0;
        } else {
          $(".fx-aa").css("backgroundPosition", "10px -190px");
          $(".flgd-dd").css("backgroundPosition", "10px -190px");
          this.qxsum = 1;
        }
    },
    fgcli() {
      var $ = this.jQuery;
        if (this.fqsum) {
          $(".fullogo").css("top", "-63px");
          $(".fulcar").css("height", "auto");
          $(".fullpri").css("left", "20px");
          $(".fullgd").css("display", "block");
          $(".fx-aa").css("opacity", "1");
          $(".fulcover").css("display", "block");
          this.fqsum = 0;
        } else {
          $(".fullogo").css("top", "-13px");
          $(".fulcar").css("height", "50px");
          $(".fullpri").css("left", "70px");
          $(".fullgd").css("display", "none");
          $(".fx-aa").css("opacity", "0");
          $(".fulcover").css("display", "none");
          this.fqsum = 1;
        }
    },
    covcli() {
      var $ = this.jQuery;
        $(".fullogo").css("top", "-13px");
        $(".fulcar").css("height", "50px");
        $(".fullpri").css("left", "70px");
        $(".fullgd").css("display", "none");
        $(".fulcover").css("display", "none");
    }
  }
};
</script>




