<template>
    <div id="regist">
        <div id="rgcontainer">
            <div class="rg-head">
                <a href="#/login" class="rgh-l" ></a>
                <span class="rgh-r"></span>
            </div>
          <el-form id="regform" :status-icon="true" :rules="rules" label-position="left" action="/login" method="get" ref="form" :model="form"
            label-width="80px">
            <el-form-item label="" prop="username">
                <el-input v-model="form.username" name="username"></el-input>
            </el-form-item>
            <el-form-item label="" prop="password">
                <el-input type="password" v-model="form.password" name="password"></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
                <el-input v-model="form.email" name="email"></el-input>
            </el-form-item>
            <el-form-item label="" prop="phone">
                <el-input v-model="form.phone" name="phone"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-radio-group v-model="form.sex">
                    <el-radio name="sex" label="1"></el-radio>
                    <el-radio name="sex" label="2"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item>
                <el-button @click="regist()" type="primary" class="btn"></el-button>
                <a href="#/login" class="relog"></a>
            </el-form-item>
          </el-form>

        <!--  -->
          <div class="agreement">
            <div class="agr-body">
              <p class="agr-title"></p>
              <div class="agr-content">
                <p class="ac-a">
                  
                  <span></span>
                </p>
                <p class="ac-b"><a href=""></a></p>
                <p class="ac-b"><a href=""></a></p>
                <p class="ac-b"><a href=""></a></p>
                <p class="ac-c">
                  <span></span>
                  
                </p>
              </div>
              <div class="agr-footer">
                <p class="af-a">
                  <span class="af-link">
                    
                    <a href=""></a>
                    <a href=""></a>
                    <a href=""></a>
                  </span>
                </p>
                <div class="af-btn">
                  <a href="#/login" class="afbtn-l"></a>
                  <a  class="afbtn-r" @click="afbtn()"></a>
                </div>
              </div>
            </div>
          </div>
        </div>
    </div>
</template>


<script>
export default {
  data() {
    return {
      form: {
        username: "",
        password: "",
        email: "",
        phone: "",
        sex: "1"
      },
      rules: {
        username: [
          {
            validator: function(rule, value, callback) {
              var usernamereg = /^\w{2,17}$/;
              if (value.trim() == "") {
                callback(new Error(""));
              } else if (!usernamereg.test(value)) {
                callback(new Error("2-18"));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        password: [
          {
            validator: function(rules, value, callback) {
              var len = value.length;
              if (value.trim() == "") {
                callback(new Error(""));
              } else if (len < 6 || len > 18) {
                callback(new Error("618"));
              } else {
                callback();
              }
            }
          }
        ],
        email: [
          {
            validator: function(rule, value, callback) {
              if (value.trim() == "") {
                callback(new Error(""));
              } else if (
                !/^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/.test(
                  value
                )
              ) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        phone: [
          {
            validator: function(rule, value, callback) {
              if (value.trim() == "") {
                callback(new Error(""));
              } else if (!/^1[3|4|5|8][0-9]\d{4,8}$/.test(value)) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    regist: function() {
      this.$refs.form.validate(function(valid) {
        if (valid) {
          //
          window.open("/#/login", "_self");
        }
      });
    },
    afbtn: function() {
      var $ = this.jQuery;
      $(".afbtn-r").click(function() {
        $(".agreement").css("display", "none");
      });
    }
  }
};
</script>

<style>
body {
  background: #f7f7f7;
}
.rg-head{
    width: 100%;
    height: 44px;
    line-height: 44px;
    background: #f9f9f9;
    color: #616161;
    font-size: 17px;
    border-bottom: 1px solid #bbb;
    z-index: 999;
    text-align: center;
}
.rgh-l{
    width: 22px;
    height: 22px;
    overflow: hidden;
    position: absolute;
    top: 12px;
    left: 12px;
    background-image: url(/static/img/iconBg.png);
    background-position: 0 -63px;
    background-repeat: no-repeat;
    background-size: 100% auto;
    display: block;
}
.rgh-r{
    color: #616161;
    font-size: 17px;
}
#regist {
  width: 100%;
  height: 100%;
}

#regform {
  padding: 20px;
}
#regform .btn {
  outline: none;
}
.relog {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  background: #409eff;
  color: white;
  text-align: center;
  box-sizing: border-box;
  outline: 0;
  margin: 0;
  margin-left: 20px;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}
.agreement {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.5);
  overflow: hidden;
  color: #333;
  display: block;
}
.agr-body {
  position: absolute;
  bottom: 0;
  padding: 0 10px;
  background: #f8f8f8;
  width: 100%;
  overflow: hidden;
  border-radius: 3px;
  box-sizing: border-box;
}
.agr-title {
  width: 100%;
  font-weight: bold;
  font-size: 18px;
  padding: 12.5px 0;
  text-align: center;
  color: black;
  margin: 0;
}
.agr-content {
  width: 100%;
  height: 177.5px;
  font-size: 13px;
  padding-top: 12.5px;
  color: black;
  overflow-y: scroll;
  overflow-x: hidden;
  box-sizing: border-box;
  text-align: left;
}
.agr-content > p {
  font-size: 13px;
  line-height: 18px;
}
.ac-a {
  margin-bottom: 10px;
}
.ac-a > span {
  font-weight: bold;
  text-decoration: underline;
}
.ac-b {
  margin: 0;
}
.ac-b > a {
  color: #000;
}
.ac-c {
  margin-top: 10px;
  margin-bottom: 0;
}
.agr-footer {
  width: 100%;
  background: #f8f8f8;
  box-sizing: border-box;
}
.agr-footer > p {
  text-align: left;
  font-size: 13px;
  padding: 10px 5px 0 0;
  box-sizing: border-box;
  margin: 0;
}
.af-link {
  font-weight: bold;
}
.af-link a {
  text-decoration: underline;
  color: #e93b3d;
}
.af-btn {
  height: 55px;
  font-size: 16px;
  text-align: center;
}
.afbtn-l,
.afbtn-r {
  display: block;
  width: 40%;
  float: left;
  background: #fff;
  border: 1px solid #d7d7d7;
  text-align: center;
  line-height: 40px;
  margin: 8px 0;
  margin-left: 10px;
  border-radius: 3px;
  text-decoration: none;
}
.afbtn-l {
  color: #333;
}
.afbtn-r {
  float: right;
  margin-left: 0;
  margin-right: 10px;
  background: #e93b3d;
  border: 1px solid #e93b3d;
  color: white !important;
}
</style>



