<template>
<div id="list">
    <div id="listcontainer">
        <div class="swiper-container list-banner">
            <div class="swiper-wrapper">
                <div class="swiper-slide"><img src="static/img/list1.png" alt="" class="swsimg"></div>
                <div class="swiper-slide"><img src="static/img/list2.png" alt="" class="swsimg"></div>
                <div class="swiper-slide"><img src="static/img/list3.png" alt="" class="swsimg"></div>
                <div class="swiper-slide"><img src="static/img/list4.png" alt="" class="swsimg"></div>
                <div class="swiper-slide"><img src="static/img/list5.png" alt="" class="swsimg"></div>
            </div>
            <div class="swiper-pagination gr"></div>   
        </div>
        <div class="list-title">
          <h1>{{listTit}}</h1>
        </div>
        <div class="list-price">
          <div class="lpr-con">
            <p class="lis-pri"><span></span>{{listPri}}</p>
            <span class="lis-r"></span>
          </div>
        </div>
        <div class="list-clearfix">
          <div class="lclear-l">
            <a href="" class="lc-a">
              <i></i>
              <span>{{listText}}				</span>
            </a>
          </div>
          <div class="lclear-r">
            <a href="" class="lc-r">
              <i></i>
              <span></span>
            </a>
          </div>
        </div>
        <div class="list-pg">
          <div class="lpg">
              <span>
                
                <span class="lpg-a">(3)</span>
              </span>
              <div class="lpg-hp">
                <div class="lpgh-a">
                  <span class="lpgh-b">
                    <div class="lpgh-c"></div>
                    <div class="lpgh-d"></div>
                  </span>
                </div>
                <span class="lpgh-f">100%</span>
              </div>
          </div>
        </div>
        <div class="list-look">
          <span class="llk"></span>
          <span class="ll-circle"><i></i></span>
        </div>
         <div class="anchor" @click="ahbm()">
            <div class="bmenu">
              <a href="/home" class="bm-aa"></a>
              <a href="/classify" class="bm-bb"></a>
              <a href="/shopcar" class="bm-cc"></a>
              <a href="/indent" class="bm-dd"></a>
              <a href="/mine" class="bm-ee"></a>
              <i class="bm-ff"></i>
            </div>
          </div>  
       <div>
      <car :shopid="guid"></car>
    </div>
  </div>

</div>
  
</template>

<script>
import "../assets/css/list.css";
import "swiper/dist/css/swiper.min.css";
import Swiper from "Swiper";
import car from "./car";
export default {
  props: ["guid", "title"],
  data() {
    return {
      shop: [],
      listImg: "",
      listPri: "",
      listTit: "",
      listText: "",
      bmnum: 1
    };
  },
  components: {
    car
  },
  methods: {
    // 
    ahbm() {
      var $ = this.jQuery;
      if (this.bmnum) {
        $(".bmenu").css("display", "block");
        this.bmnum = 0;
      } else {
        $(".bmenu").css("display", "none");
        this.bmnum = 1;
      }
    }
  },
  mounted() {
    var mySwiper = new Swiper(".swiper-container", {
      autoplay: true, //
      pagination: {
        el: ".gr",
        type: "fraction"
      }
    });
  },
  created() {
    //
    if (!this.bus.cars[this.guid]) {
      this.$set(this.bus.cars, this.guid, []);
    }
    this.jQuery.get(this.apiURL + "/shops", result => {
      this.shop = result.list;
      this.listImg = result.list.list_img;
      this.listPri = result.list.list_price;
      this.listTit = result.list.list_tit;
      this.listText = result.list.list_text;
    });
  }
};
</script>



